
import { defineComponent, ref } from 'vue'
import { ICM21002SC01_RES_Detail, ICM21002SC01_REQ_Edit, ICM21002SC01_REQ_Register, ICM21002SC01_REQ_Details, ICM21002SC01_RES_Details } from "@/shared/type/icm212002sc01.type";
import { modalController } from '@ionic/vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
    name: "icm212002sc01",
    components: {
        BreadCrumb,
        ModifyCard
    },

    data() {
        const modifyCard = {
            registerTellerId: "",
            registerTellerName: "",
            registerDate: "",
            registerTime: "",
            updatedTellerId: "",
            updatedTellerName: "",
            updatedDate: "",
            updatedTime: "",
            deleteTellerId: "",
            deleteTellerName: "",
            deleteDate: "",
            deleteTime: "",
        }

        return {
            productCategoryDetail: ref<ICM21002SC01_RES_Detail>({
                categoryCode: "003",
                categoryDesc: "",
                categoryName: "",
                deleteBranchCode: "",
                deleteDate: "",
                deleteTellerID: "",
                deleteTellerName: "",
                deleteTime: "",
                firstRegisterBranchCode: "",
                firstRegisterDate: "",
                firstRegisterTellerID: "",
                firstRegisterTellerName: "",
                firstRegisterTime: "",
                lastChangeBranchCode: "",
                lastChangeDate: "",
                lastChangeTellerID: "",
                lastChangeTellerName: "",
                lastChangeTime: "",
                statusCode: "",
            }),
            isEdit: ref<boolean>(true),
            isRequired: ref<boolean>(false),
            modifyCard,
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode,
        }
    },

    async created() {
        if (this.approvalType === '') {
            switch (this.$route.query.type) {
                case 'register':
                    this.isEdit = false;
                    break;
                default:
                    await this.requestProductCategoryDetail();
                    break;
            }
        } else {
            await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body

                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

                this.productCategoryDetail.categoryCode = authDetailParsed.categoryCode
                this.productCategoryDetail.categoryName = authDetailParsed.categoryName
                this.productCategoryDetail.categoryDesc = authDetailParsed.categoryDesc
            })
        }
    },

    computed: {
        isDelete(): boolean {
            return this.$route.query.type == "edit" && this.isEdit ? true : false;
        },
        isCancel(): boolean {
            return this.$route.query.type == "register" || !this.isEdit ? true : false;
        },
        isList(): boolean {
            return this.$route.query.type == "edit" && this.isEdit ? true : false;
        },
        isEditable(): boolean {
            return this.$route.query.type == "edit" && this.isEdit ? true : false;
        },
        isSave(): boolean {
            return this.$route.query.type == "register" || !this.isEdit ? true : false;
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
    },

    methods: {
        async requestProductCategoryDetail() {
            const body = {
                categoryCode: String(this.$route.query.categoryCode)
            } as ICM21002SC01_REQ_Details

            const res = await this.$request.getRequest('ICM21200212', body, false) as ICM21002SC01_RES_Details

            if (res.header.result) {

                this.productCategoryDetail = JSON.parse(JSON.stringify(res.body))

                this.modifyCard = {
                    registerTellerId: String(this.productCategoryDetail.firstRegisterTellerID),
                    registerTellerName: String(this.productCategoryDetail.firstRegisterTellerName),
                    registerDate: String(this.productCategoryDetail.firstRegisterDate),
                    registerTime: String(this.productCategoryDetail.firstRegisterTime),
                    updatedTellerId: this.productCategoryDetail.statusCode === '09' ? "" : res.body.lastChangeTellerID,
                    updatedTellerName: this.productCategoryDetail.statusCode === '09' ? "" : res.body.lastChangeTellerName,
                    updatedDate: this.productCategoryDetail.statusCode === '09' ? "" : res.body.lastChangeDate,
                    updatedTime: this.productCategoryDetail.statusCode === '09' ? "" : res.body.lastChangeTime,
                    deleteTellerId: this.productCategoryDetail.statusCode === '09' ? res.body.lastChangeTellerID : "",
                    deleteTellerName: this.productCategoryDetail.statusCode === '09' ? res.body.lastChangeTellerName : "",
                    deleteDate: this.productCategoryDetail.statusCode === '09' ? res?.body.lastChangeDate : "",
                    deleteTime: this.productCategoryDetail.statusCode === '09' ? res?.body.lastChangeTime : "",
                }
            }

        },

        async requestProductCategoryRegister() {
            const body: ICM21002SC01_REQ_Register = {
                categoryCode: this.productCategoryDetail.categoryCode,
                categoryName: this.productCategoryDetail.categoryName,
                categoryDesc: this.productCategoryDetail.categoryDesc,
            }

            const res = await this.$request.getRequest("ICM21200221", body, false) as ICM21002SC01_RES_Details

            if (res.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            }

            return res.header.result;
        },

        async requestProductCategoryEdit() {
            const body: ICM21002SC01_REQ_Edit = {
                categoryCode: this.productCategoryDetail.categoryCode,
                categoryName: this.productCategoryDetail.categoryName,
                categoryDesc: this.productCategoryDetail.categoryDesc,
            }

            const res = await this.$request.getRequest("ICM21200231", body, false) as ICM21002SC01_RES_Details

            if (res.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: "Success",
                    callback: () => {
                        this.requestProductCategoryDetail();
                        modalController.dismiss();
                    },
                });
            }

            return res.header.result;
        },

        async requestProductCategoryDelete() {
            const body = { categoryCode: this.productCategoryDetail.categoryCode } as ICM21002SC01_REQ_Details;

            const res = await this.$request.getRequest("ICM21200241", body, false) as ICM21002SC01_RES_Details;
            if (res.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            }

            return res.header.result;
        },

        handleSaveAction() {
            if (!this.productCategoryDetail.categoryName.trim() || !this.productCategoryDetail.categoryCode.trim()) {
                return this.isRequired = true;
            }

            this.$dialog.onOpen({
                content: this.$t('message.would_you_like_to_save'),
                dialogMode: "Question",
                callback: () => {
                    switch (this.$route.query.type) {
                        case 'register':
                            this.requestProductCategoryRegister().then((res: boolean) => {
                                if (res) {
                                    modalController.dismiss();
                                    this.$router.push("/ICM212002SC00");
                                }
                            });
                            break;
                        default:
                            this.requestProductCategoryEdit().then(() => {
                                this.requestProductCategoryDetail();
                                this.isEdit = true;
                                modalController.dismiss();
                            });
                            break;
                    }

                    modalController.dismiss();
                }
            })
        },

        validateLength(event: KeyboardEvent) {
            const inputValue = (event.target as HTMLInputElement).value;

            if (inputValue.length > 3) {
                return;
            }

            if (event.keyCode === 8 || event.key === 'Backspace' && event.keyCode !== 13 || !(/\d/.test(event.key))) {
                return event.preventDefault()
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'list':
                    this.$router.push("/ICM212002SC00");
                    break;
                case 'edit':
                    this.isEdit = false;
                    break;
                case 'cancel':
                    if (this.$route.query.type === "edit") {
                        this.$dialog.onOpen({
                            content: this.$t('message.cancel_without_saving_changes'),
                            dialogMode: "Question",
                            callback: () => {
                                modalController.dismiss();
                                this.isEdit = true;
                                this.requestProductCategoryDetail();
                            }
                        })
                    } else {
                        this.$dialog.onOpen({
                            content: this.$t('message.cancel_without_saving_changes'),
                            dialogMode: "Question",
                            callback: () => this.$router.back()
                        })
                    }
                    break;
                case 'delete':
                    this.$dialog.onOpen({
                        content: this.$t('message.are_you_sure_you_want_to_delete'),
                        dialogMode: "Question",
                        callback: () => {
                            this.requestProductCategoryDelete().then((res) => {
                                if (res) {
                                    this.$router.push("/ICM212002SC00");
                                    modalController.dismiss();
                                }
                            });
                            modalController.dismiss();
                        }
                    })
                    break;
                case 'save':
                    this.handleSaveAction();
                    break;
            }
        }
    }
})
